ionUrl(app)\">\n\t\t\t\t{{ app.id }}. {{ app.app_id }} (token: {{ app.token }})\n\t\t\t</a>\n\t\t</div>\n\t\t<div v-else>\n\t\t\t<b>{{ t('cloud_py_api', 'No apps registered') }}</b>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\n\nexport default {\n\tname: 'Configuration',\n\tdata() {\n\t\treturn {\n\t\t\tapps: [],\n\t\t}\n\t},\n\tbeforeMount() {\n\t\tthis.getApps()\n\t},\n\tmounted() {\n\t\tthis.$emit('update:loading', false)\n\t},\n\tmethods: {\n\t\tgetApps() {\n\t\t\tthis.$emit('update:loading', true)\n\t\t\taxios.get(generateUrl('/apps/cloud_py_api/api/v1/apps')).then(res => {\n\t\t\t\tthis.apps = res.data\n\t\t\t\tthis.$emit('update:loading', false)\n\t\t\t}).catch(err => {\n\t\t\t\tconsole.debug(err)\n\t\t\t\tthis.$emit('update:loading', false)\n\t\t\t})\n\t\t},\n\t\tgetAppConfigurationUrl(app) {\n\t\t\treturn generateUrl(`/apps/cloud_py_api/apps/${app.id}`)\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.cloud_py_api-configuration {\n\tmargin: 20px;\n\ttext-align: center;\n}\n\nh2 {\n\tmargin: 20px 0;\n}\n\n.apps-list {\n\tmargin: 20px 0;\n}\n\n.registered-app {\n\tborder: 1px solid #eee;\n\tpadding: 10px 15px;\n\tmargin: 10px 0;\n\tborder-radius: 5px;\n}\n</style>\n"],"sourceRoot":""}]);
